import { TextField, Box, makeStyles, Button, Typography } from '@material-ui/core';
import { useState } from 'react';

// styling
const useStyle = makeStyles({
    container:{
        width:'100%',
        height:'100vh',
        backgroundColor: '#bdc3c7',
        display:'flex',
        justifyContent:'center',
        alignItems:'center'
    },
    inputBox: {
        width: '25%',
        backgroundColor: '#ffffff',
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        alignItems:'center',
        borderRadius:10,
        padding:'11px 15px 25px 15px',
        '& > *' : {
            marginTop:22,
            width:'80%',
        }
    },
    error:{
        color:"red",
        fontSize:14,
        fontWeight:600,
        marginTop:0
    }
})

const InitialValue={
    firstname:'',
    lastname:'',
    email:'',
    password:'',
    repassword:''
}

const InputUI = () => {
    const [inputValue, setInputValue] = useState(InitialValue);
    const [warns, setwarns ]          = useState("")
    const [act, setact ]              = useState(false)
    const classes = useStyle()
    const { firstname, lastname, email, password, repassword } = inputValue;


    const onInputChange =  (e) => {
       setInputValue({...inputValue, [e.target.name]: e.target.value});
    }

    const checkPassword = () => {
        if(repassword.length > 0){
            if(password === repassword) {
               setwarns("")
               setact(true);
            }else {
                setwarns("confirm the correct password");
               setact(false);
            }
        } else {
            setwarns("re-enter the password");
            setact(false);
        }
    }
    const checkPassword2 = () => {
            if(password === repassword) {
               setwarns("");
               setact(true);
            }else {
                setwarns("confirm the correct password");
               setact(false);
            }
        
    }
    const showData= () => {
        // Object.values(inputValue).forEach(val => console.log(val));
        const keys = Object.keys(inputValue);
        keys.forEach((key, index) => {
            console.log(`${key}: ${inputValue[key]}`);
        });
    }

    return (
      <>
        <Box className={classes.container}>
            <Box className={classes.inputBox}>
                <Typography variant='h4' style={{fontWeight:700,textAlign:'center'}} color='primary'>SIGN UP</Typography>
                <TextField  onChange={(e) => onInputChange(e) } type="text"  placeholder="Enter First Name" name="firstname" value={firstname}/>
                <TextField  onChange={(e) => onInputChange(e) } type="text"  placeholder="Enter Last Name" name="lastname" value={lastname}/>
                <TextField  onChange={(e) => onInputChange(e) } type="text"  placeholder="Enter Email" name="email" value={email}/>
                <TextField  onChange={(e) => onInputChange(e) } onKeyUp={()=> checkPassword2()} type="password"  placeholder="Enter Password" name="password" value={password}/>
                <TextField  onChange={(e) => onInputChange(e) } onKeyUp={()=> checkPassword()} type="password" placeholder="Re-Enter Password"  name="repassword" value={repassword}/>
                <Typography className={classes.error}>{warns}</Typography>
                {
                    act ? <Button variant="contained" onClick={() => showData()} color="primary">Submit</Button> 
                : <Button variant="contained"  color="primary" disabled>Submit</Button>
                }
            </Box>
        </Box>
      </>
    )
}

export default InputUI;

